.emptyIconContainer{
    width: 100%;
   height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-block-start: 80px;
}

.emptyIcon{
    width: 10px;
    height: 40px;
    border-radius: 5px;
    background-color: #ADAC9A;
    margin:  6px;
    animation: move 1.1s infinite ease-in-out;

}

.emptyIcon-1{
    transform: rotate(-45deg);

}

.emptyIcon-2{
    transform: rotate(45deg);
    
}

@keyframes move {
    0%{
        margin-top: 0%;
    }
    50%{
        margin-top: 100px;
    }
    100%{
        margin-top: 0;
    }
}